body {
    background-color: #f6dcaf;
}

.app-wrapper {
  width: auto;
  margin: 0 auto;
  display: grid;
  background-color: #f6dcaf;

  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
}

.app-wrapper-content {
  grid-area: c;
  background-color: #f3e4ca;
    border-radius: 10px;
    margin: 10px;
}